body {
    background-color: whitesmoke;
}

.todo-list {
    background-color: white;
    margin: auto;
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid #efefef;
    box-shadow: /* The top layer shadow */
    0 1px 1px rgba(0, 0, 0, 0.15), /* The second layer */
    0 10px 0 -5px #eee, /* The second layer shadow */
    0 10px 1px -4px rgba(0, 0, 0, 0.15), /* The third layer */
    0 20px 0 -10px #eee, /* The third layer shadow */
    0 20px 1px -9px rgba(0, 0, 0, 0.15);
    padding: 30px;
}

.todo-item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 30px 20px 0;
    width: 70%;
    border-bottom: 1px solid #cecece;
    font-family: Roboto, sans-serif;
    font-weight: 100;
    font-size: 15px;
    color: #333333;
}

input[type="checkbox"] {
    margin-right: 10px;
    font-size: 30px;
}

input[type="checkbox"]:focus {
    outline: 0;
}

.myInput {
    padding: 4px;
    font-size: 19px;
    border-width: 1px;
    border-color: #cccccc;
    background-color: #ffffff;
    color: #020202;
    border-style: solid;
    box-shadow: 0px 0px 1px rgba(66, 66, 66, 0.75);
    text-shadow: 0px 0px 5px rgba(66, 66, 66, 0.75);
    margin-right: 2px;
}

.myInput:focus {
    outline: none;
}

.myButton {
    box-shadow: 0px 2px 0px 0px #f0f7fa;
    background: linear-gradient(to bottom, #33bdef 5%, #019ad2 100%);
    background-color: #33bdef;
    border: 1px solid #057fd0;
    display: inline-block;
    cursor: pointer;
    color: #ffffff;
    font-family: Arial;
    font-size: 19px;
    font-weight: bold;
    padding: 5px 24px;
    text-decoration: none;
    text-shadow: 0px -1px 0px #5b6178;
}

button:hover {
    background: linear-gradient(to bottom, #019ad2 5%, #33bdef 100%);
    background-color: #019ad2;
}

button:active {
    position: relative;
    top: 3px;
}

.newItem {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
}

.delete {
    margin-left: auto;
    box-shadow: 0px 2px 0px 0px #f0f7fa;
    background: linear-gradient(to bottom, #33bdef 5%, #019ad2 100%);
    background-color: #33bdef;
    border: 1px solid #057fd0;
    display: inline-block;
    cursor: pointer;
    color: #ffffff;
    font-family: Arial;
    font-size: 10px;
    font-weight: bold;
    padding: 5px 10px;
    text-decoration: none;
    text-shadow: 0px -1px 0px #5b6178;
}